//API
import requests from "./request";
import mock from "./mock";

//
// /api/product/getBaseCategoryList get 

//:axiosPrmiose
export const reqCategoryList = () => {
  return requests({
    url: "/product/getBaseCategoryList",
    method: "get",
  });
};
//bannerhome
export const reqGetBannerList = () => mock.get("/banner");
//floor
export const reqGetFloorList = () => mock.get("/floor");
//search() api/list :post 
export const reqGetSearchInfo = (parmas) => {
  return requests({
    url: "/list",
    method: "post",
    data: parmas,
  });
};
//Detail/api/item/{ skuId } GET
export const reqGoodsInfo = (skuId) => {
  return requests({
    url: `/item/${skuId}`,
    method: "get",
  });
};
//  GET /api/cart/addToCart/{ skuId }/{ skuNum }
export const reqAddOrUpdateShopCart = (skuId, skuNum) => {
  return requests({
    url: `/cart/addToCart/${skuId}/${skuNum}`,
    method: "post",
  });
};
//  GET /api/cart/carLsit
export const reqCartList = () => {
  return requests({
    url: "/cart/cartList",
    method: "get",
  });
};
// /api/cart/deleteCart/{skuId} DELETE
export const reqDeleteCreatById = (skuId) => {
  return requests({
    url: `/cart/deleteCart/${skuId}`,
    method: "delete",
  });
};
// /api/cart/checkCart/{skuID}/{isChecked} GET
export const reqUpdateCheckById = (skuId, isChecked) => {
  return requests({
    url: `/cart/checkCart/${skuId}/${isChecked}`,
    method: "get",
  });
};
